import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userRole: '', // 
    permissions: {
      '': ['attendance', 'leave', 'salary', 'reward', 'report'],
      '': ['review_attendance', 'review_leave'],
      '': ['full_access'],
      '': ['admin']
    }
  },
  getters: {
    allowedModules: (state) => {
      return state.permissions[state.userRole] || []
    }
  },
  mutations: {
    setUserRole(state, role) {
      state.userRole = role
    }
  },
  actions: {
    updateRole({ commit }, role) {
      commit('setUserRole', role)
    }
  }
})